.card {
    -webkit-box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 
                0 3px 1px -2px rgba(0, 0, 0, 0.2), 
                0 1px 5px 0 rgba(0, 0, 0, 0.12);
            box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 
                0 3px 1px -2px rgba(0, 0, 0, 0.2), 
                0 1px 5px 0 rgba(0, 0, 0, 0.12);
    padding: 5px 10px;
    margin-bottom: 20px;
    background-color: white;
}

.card .cardHeader {
    border-bottom: 1px solid rgba(0,0,0,0.1);
    padding-top: 5px;
    padding-bottom: 10px;
    display: -ms-flexbox;
    display: flex;
}

.card .cardHeader .userImg .img-circle {
    height: 50px;
    width: 50px;
}

.card .cardHeader .textRight {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    width: 100%;
    -ms-flex-line-pack: justify;
        align-content: space-between;
    padding-left: 10px;
}

.card .cardHeader .textRight>div {
    width: 100%;
}

.card .cardHeader .textRight .userName {
    font-weight: bold;
    font-size: 16px;
}

.card .cardContainer .postBody {
    padding-top: 10px;
    padding-bottom: 5px;
}


