.statsCard {
    display: -ms-flexbox;
    display: flex;
    height: 50px;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    /* box-shadow: 0px 2px 7px 3px rgba(0,0,0,0.15); */
    -webkit-box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 
                0 3px 1px -2px rgba(0, 0, 0, 0.2), 
                0 1px 5px 0 rgba(0, 0, 0, 0.12);
            box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 
                0 3px 1px -2px rgba(0, 0, 0, 0.2), 
                0 1px 5px 0 rgba(0, 0, 0, 0.12);
}

.statsCard > div {
    height: 50px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
}

.statsCard .textWrap {
    padding: 0 0 0 10px;
    width: 70%;
}

.statsCard .textWrap .text {
    -ms-flex-item-align: center;
        align-self: center;
    font-size: 16px;
    line-height: 24px;
}

.statsCard .numberWrap {
    -ms-flex-pack: end;
        justify-content: flex-end;
    padding: 0 10px 0 0;
    width: 30%;
}

.statsCard .numberWrap .number {
    -ms-flex-item-align: center;
        align-self: center;
    font-size: 24px;
    line-height: 24px;
}


